//?	Reset (WPF2)
//!	Canceladores de configurações de estilo padrão de browsers:

/* 
	http://meyerweb.com/eric/tools/css/reset/ 
   	v2.0 | 20110126
   	License: none (Public Domain)
*/

html,		body,		div,
span,		applet,		object,
iframe,		h1,			h2,
h3,			h4,			h5,
h6,			p,			blockquote,
pre,		a,			abbr,
acronym,	address,	big,
cite,		code,		del,
dfn,		em,			img,
ins,		kbd,		q,
s,			samp,		small,
strike,		strong,		sub,
sup,		tt,			var,
b,			u,			i,
center,		dl,			dt,
dd,			ol,			ul,
li,			fieldset,	form,
label,		legend,		table,
caption,	tbody,		tfoot,
thead,		tr,			th,
td,			article,	aside,
canvas,		details,	embed, 
figure,		figcaption,	footer,
header,		hgroup,		menu,
nav,		output,		ruby,
section,	summary,	time,
mark,		audio,		video{	
    border: 0;
	margin: 0;
    padding: 0;
    font-size: 100%;
    vertical-align: baseline;
}

article,	aside,		details,
figcaption,	figure,		footer,
header,		hgroup,		menu,
nav,		section{
    display: block;
}

a{
	text-decoration: none;
}

body{
    line-height: 1;
}

ol, ul{
    list-style: none;
}

blockquote, q{
    quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after{
    content: '';
    content: none;
}

table{
    border-collapse: collapse;
    border-spacing: 0;
}
